export default{
	methods:{
		gophone(){
			wx.navigateTo({
				url:'../LoginPhone/loginphone'
			})
		},
		goemail(){
			wx.navigateTo({
				url:'../LoginEmail/loginemail'
			})
		},
		bindGetUserInfo(res){
			if(res.mp.detail.userInfo){
				console.log(res.mp.detail.userInfo)
				wx.showToast({
					title:'登录成功',
					// duration: 5000,  // 貌似不生效
					success: () => {
						// reLaunch解决跳转能tabBar传参的问题
						wx.setStorage({
							'key':'isLogin',
							'data':true
						})
						wx.reLaunch({
							url: '/pages/Ucenter/ucenter'
						})
					}
				})
				
			}else {

				wx.showToast({
					title: '授权失败，请重新授权'
				})
			}	
		}
		
	}
	
}
